import React, { Component } from 'react';
import './App.css';
import Welcome from './components/Welcome'
import Datos from'./components/Datos'
import Curriculum from'./components/Curriculum'
import Temario from'./components/Temario'
import Horario from'./components/Horario'
import Nav from'./components/Nav'

class App extends Component {

  state = {
    pages: [
      {id:"01", name: "Welcome", active: true},
      {id:"02", name: "Temario", active: false},
      {id:"03", name: "Datos", active: false},
      {id:"04", name: "Curriculum", active: false},
      {id:"05", name: "Horario", active: false}
    ],
    showPage: "Datos"
  }

  toggleActivePage = (event, id) =>{
    let texto = event.target.firstChild.textContent
    let pageIndex = null
    switch(texto){
      case "Inicio":
        pageIndex = "03"
        break
      case "Temario":
        pageIndex = "02"
        break
      case "Datos":
        pageIndex = "03"
        break
      case "Curriculum":
        pageIndex = "04"
        break
      case "Horario":
        pageIndex = "05"
        break
      default:
        pageIndex = "01"
        break
    }
    const pageIndexf = this.state.pages.findIndex(p =>{
      return p.id === pageIndex
    })
    const fpage = {...this.state.pages[pageIndexf]}
    fpage.active = true

    let pages = [...this.state.pages]
    pages[pageIndexf] = fpage
    this.setState({pages:pages})
    this.setState({showPage:texto})


    // console.log(pageIndex)
    // console.log(fpage)
  }


  render() {

    let activePage = null

    if (this.state.showPage === "Inicio"){
      activePage = (
        <div>
          <Datos />
        </div>
      )
    }
    else if (this.state.showPage === "Temario"){
      activePage = (
        <div>
          <Temario />
        </div>
      )
    }
    else if (this.state.showPage === "Datos"){
      activePage = (
        <div>
          <Datos />
        </div>
      )
    }
    else if (this.state.showPage === "Curriculum"){
      activePage = (
        <div>
          <Curriculum />
        </div>
      )
    }
    else if (this.state.showPage === "Horario"){
      activePage = (
        <div>
          <Horario />
        </div>
      )
    }
    else {
      activePage = (
        <div>
          <Welcome />
        </div>
      )
    }


    return (
      <div className="App">
        <Nav click={(event)=> this.toggleActivePage(event)} />
        {activePage}
      </div>
    );
  }
}

export default App;
