import React from 'react'
import './css/Nav.css'
const Nav = (props)=>{
    return(
        <div className="nav-bar">
            <div className="inner-bar">
                <ul>
                    <li><a href="#" onClick={props.click}>Inicio</a> </li>
                    <li><a href="#" onClick={props.click}>Temario</a></li>
                    <li><a href="#" onClick={props.click}>Curriculum</a></li>
                    <li><a href="#" onClick={props.click}>Horario</a></li>
                </ul>
            </div>
        </div>
    )
}

export default Nav